import Coupon from "../models/Coupon";

export class CartState {
    public coupons: Coupon[] = [];

}

export enum CartActionType {
    AddToCart = "AddToCart",
    RemoveFromCart = "RemoveFromCart",
    RestCart = "RestCart",
}

// Action declaration
export interface CartAction {
    type: CartActionType,
    payload?: any,
}

// Action functions


export function addToCart(coupon: Coupon): CartAction {
    return { type: CartActionType.AddToCart, payload: coupon }
}

export function removeFromCart(id: number): CartAction {
    return { type: CartActionType.RemoveFromCart, payload: id }
}

export function restCart(): CartAction {
    return { type: CartActionType.RestCart, payload: null }
}

// Reducer 
export function cartReducer(currentState: CartState = new CartState(), action: CartAction): CartState {
    const newState = { ...currentState };

    switch (action.type) {
        case CartActionType.AddToCart:
            newState.coupons.push(action.payload)
            break;
        case CartActionType.RemoveFromCart:
            let toRemove = newState.coupons.findIndex((item)=>{item.id = action.payload})
            newState.coupons.splice(toRemove)
            break;
        case CartActionType.RestCart:
            newState.coupons = [];
            break
    }
    return newState;
}




